/*
 * header
 */
#header {
    position: relative;
    z-index: 99997;
    height: 38px;
	padding: 0 30px;
    margin-bottom: 20px;
    border-bottom: 1px solid #e0dede;
    background-color: #f7f7f7;
    background: -webkit-gradient(linear, left top, left bottom, from(#fff), to(#f7f6f5));
    background: linear-gradient(#fff, #f7f6f5);
    background: -webkit-linear-gradient(#fff, #f7f6f5);
    -webkit-box-shadow: 0 -1px 0 #fefcfc inset,0 0 4px rgba(0,0,0,.15);
            box-shadow: 0 -1px 0 #fefcfc inset,0 0 4px rgba(0,0,0,.15);
    -moz-shadow: 0 -1px 0 #fefcfc inset,0 0 4px rgba(0,0,0,.15);
    -webkit-shadow: 0 -1px 0 #fefcfc inset,0 0 4px rgba(0,0,0,.15);
}
#header h1 {
    font-size: 16px;
    line-height: 38px;
    margin: 0;
}

/*
 * waterfall
 */
.item {
    /* position: absolute; */
    /* top: 0; */
    z-index: 1;
    width: 192px;
    padding: 15px;
    line-height: 1.5;
    background-color: #fff;
    box-shadow: 0 1px 3px rgba(34, 25, 25, .4);
    -moz-box-shadow: 0 1px 3px rgba(34, 25, 25, .4);
    -webkit-box-shadow: 0 1px 3px rgba(34, 25, 25, .4);
    filter: progid:DXImageTransform.Microsoft.Shadow(color = #adacac, direction = 135, strength = 2);
}
.item img {
    display: block;
}
.item:hover {
    box-shadow: 0 1px 5px rgba(34, 25, 25, .8);
    -moz-box-shadow: 0 1px 5px rgba(34, 25, 25, .8);
    -webkit-box-shadow: 0 1px 5px rgba(34, 25, 25, .8);
    filter: progid:DXImageTransform.Microsoft.Shadow(color = #979797, direction = 135, strength = 3);
}
#trigger {
	padding: 10px 30px 20px 30px;
}
#trigger a { margin-right: 30px;
}

/*
 * page-navigation
 */
#page-navigation {
	text-align: center;
	padding: 20px 0 50px 0;
    display: none;
}
#page-navigation a {
	width:32px;
	height: 32px;
	line-height: 32px;
	display: inline-block;
	border: 1px #e4e4e4 solid;
	text-decoration: none;
}
#page-navigation .page-navigation-prev, #page-navigation .page-navigation-next {
	display: inline-block;
	height: 32px;
	line-height: 32px;
	width: 80px !important;
}
#page-navigation a:hover {
	border: 1px #c30 solid;
	background: #c30;
	color: #fff;
}

/* #page-navigation a:active {
} */
#page-navigation a.cur {
	border: 1px #c30 solid;
	background: #c30;
	color: #fff;
}
#page-navigation span.disabled {
	color: #ccc;
	border: 1px #e4e4e4 solid;
}
.title {
  text-align: center;
  color: #000;
  text-shadow: 4px 2px 2px #ddd;
  font: 700 30px '\5FAE\8F6F\96C5\9ED1';
  background-color: #999;
  padding: 10px;
  border: 6px solid #ccc;
  position: relative;
}
.title span {
  display: block;
  position: absolute;
  top: 8px;
  left: 100px;
  -webkit-animation: arrow 1s ease-in-out infinite;
          animation: arrow 1s ease-in-out infinite;
}
@-webkit-keyframes arrow {
0% {
    -webkit-transform: translateX(100px);
            transform: translateX(100px);
}
50% {
    -webkit-transform: translateX(160px);
            transform: translateX(160px);
}
100% {
    -webkit-transform: translateX(100px);
            transform: translateX(100px);
}
}
@keyframes arrow {
0% {
    -webkit-transform: translateX(100px);
            transform: translateX(100px);
}
50% {
    -webkit-transform: translateX(160px);
            transform: translateX(160px);
}
100% {
    -webkit-transform: translateX(100px);
            transform: translateX(100px);
}
}
#container {
  margin: 10px auto;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
#container .item {
  float: left;
  padding: 10px;
  margin: 10px;
  border: 1px solid #ccc;
  background: #f7f7f7;
  -webkit-box-shadow: 10px 10px 8px #ccc;
          box-shadow: 10px 10px 8px #ccc;
  overflow: hidden;
}
#container .item:hover {
  -webkit-box-shadow: 16px 16px 20px #999;
          box-shadow: 16px 16px 20px #999;
}
#container .item img {
  width: 240px;
  -webkit-transition: all 0.6s;
  transition: all 0.6s;
}
#container .item:hover img {
  -webkit-transition: all 1s;
  transition: all 1s;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

